/* Make the submit button span both columns */

button[type="submit"] {
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
}

button[type="submit"]:hover {
    background-color: #45a049;
}

.submit-button-container {
    grid-column: 1 / span 2;
    grid-row: 5;
    display: flex;
    justify-content: center;
    padding-top: 10px;
    padding-bottom: 10px;
}


/* Faded placeholder color */

input::placeholder {
    color: #aaa;
}